import React, { useState, useEffect } from "react"
import MealList from "./MealList/MealList"
import "./MealApp.css";

function MealApp() {
  const [mealData, setMealData] = useState(null)

  
  useEffect(()=>{
    getMealData();
  }, []) 

  function getMealData() {
    
    fetch(
      `https://api.spoonacular.com/recipes/random?apiKey=ac7ca3f07ba846cd9c8052844192f83f&number=10&tags=vegan`
    )
      .then(response => response.json())
      .then(data => {
        setMealData(data)
      })
      .catch(() => {
        console.log("error")
      })
      
  }

  return (
    <div className="App">
      {mealData && <MealList mealData={mealData} />}
    </div>
  )
}

export default MealApp