import * as React from 'react';
import { styled } from '@mui/material/styles';
import Card from '@mui/material/Card';
import CardHeader from '@mui/material/CardHeader';
import CardMedia from '@mui/material/CardMedia';
import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';
import Collapse from '@mui/material/Collapse';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import FavoriteIcon from '@mui/icons-material/Favorite';
import ShareIcon from '@mui/icons-material/Share';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import Grid from '@mui/material/Grid';
import { pink } from '@mui/material/colors';

const ExpandMore = styled((props) => {
  const { expand, ...other } = props;
  return <IconButton {...other} />;
})(({ theme, expand }) => ({
  transform: !expand ? 'rotate(0deg)' : 'rotate(180deg)',
  marginLeft: 'auto',
  transition: theme.transitions.create('transform', {
    duration: theme.transitions.duration.shortest,
  }),
}));


export default function RecipeReviewCard( { meal }) {
  const [expanded, setExpanded] = React.useState(false);
  const [liked, setLiked] = React.useState(false);

  const handleExpandClick = () => {
    setExpanded(!expanded);
  };

  const handleLike = () => {
    setLiked(!liked);
  };

  const handleShare = () => {
    console.log('handleShare');
    navigator
      .share({
          title: meal.title,
          text: meal.summary,
          url: meal.sourceUrl
      })
      .then(() => console.log('Successful share! ðŸŽ‰'))
      .catch(err => console.error(err));
  }

  return (
    <Card sx={{ maxWidth: 556 }}>
      <CardHeader
        title={meal.title}
      />
      <CardMedia
        component="img"
        image={meal.image}
        alt={meal.title}
      />
      <CardActions disableSpacing>
        <IconButton aria-label="add to favorites">
          <FavoriteIcon sx={liked ? { color: pink[500] } : {} } onClick={handleLike} />
        </IconButton>
        <IconButton aria-label="share">
          <ShareIcon  onClick={handleShare}/>
        </IconButton>
        <ExpandMore
          expand={expanded}
          onClick={handleExpandClick}
          aria-expanded={expanded}
          aria-label="show more"
        >
          <ExpandMoreIcon />
        </ExpandMore>
      </CardActions>
      <Collapse in={expanded} timeout="auto" unmountOnExit>
        <CardContent>
          <Typography variant="body2" paragraph>Ingredients:</Typography>
          <div className="container">
          <Grid container spacing={1}>
          {meal.extendedIngredients.map(ingr => {
            return (
              <Grid item xs={6}>
                <Typography className="ingredient" variant="body2" color="text.secondary">
                {ingr.name}
              </Typography>
              </Grid>
            )
            })}
            </Grid>
        </div>
        </CardContent>
      </Collapse>
      <CardContent>
        <Typography variant="body2" color="text.secondary">
        <div dangerouslySetInnerHTML={{ __html: meal.summary }}></div>
        </Typography>
      </CardContent>
      
    </Card>
  );
}
